import React from "react";
import { validURL } from "../helpers";

function Label(props) {
  const handleLabelInput = (id, value) => {
    localStorage.setItem("VRA_" + id, value);
  };

  const handleLinkInput = (id, value) => {
    localStorage.setItem("VRA_LINK_" + id, value);
  };

  const verifyExistingLink = (id, value) => {
    if (!validURL(value)) {
      alert(
        "Your URL value is Invalid, please make sure it is valid before proceding"
      );
      localStorage.removeItem("VRA_LINK_" + id);
    }
  };

  return (
    <React.Fragment>
      <p>
        {props.key2.event} | ID: {props.key2.richContext}
      </p>
      <div className="richContextContainer">
        <p className="editorLabel">Label: </p>
        <input
          className="richContextEditor"
          type="text"
          defaultValue={props.labelValue}
          onInput={(e) =>
            handleLabelInput(props.key2.richContext, e.target.value)
          }
        />
      </div>
      <div className="richContextContainer">
        <p className="editorLabel">Link: </p>
        <input
          className="richContextEditor"
          type="text"
          defaultValue={props.linkValue}
          onInput={(e) =>
            handleLinkInput(props.key2.richContext, e.target.value)
          }
          onBlur={(e) =>
            verifyExistingLink(props.key2.richContext, e.target.value)
          }
        />
      </div>
    </React.Fragment>
  );
}

export default Label;
