import React, { useState, useEffect } from "react";
import Countdown from "./Countdown";
import Period from "./Period";
import SettingsIcon from "./SettingsIcon";
import { convertToEST } from "./helpers";
import { defaultTimes } from "./defaults/defaults";

import "./App.css";

function AppContent() {
  // time
  const [time, setTime] = useState(new Date());
  // display the app, used by the modal to hide the app
  const [displayAppContents, setDisplayAppContents] = useState(true);

  // timeing stuff
  const [timesTable, setTimesTable] = useState(null);
  const [isWeekend, setIsWeekend] = useState(false);

  // slots
  const [currentSlot, setCurrentSlot] = useState(undefined);
  const [nextSlot, setNextSlot] = useState(undefined);

  // color checks
  const [appStyles, setAppStyles] = useState(null);
  const [appClass, setAppClass] = useState(null);

  const colorFix = () => {
    // because states dont reload if the undersource changes
    // we have to do that manualy
    // TODO: Any Better Way to do it?
    const colorMode = localStorage.getItem("VRA_COLOR_MODE");
    if (colorMode === null || colorMode === "color") {
      // color set specifically or never ever set mode
      const loadedBackgroundColor = localStorage.getItem("VRA_COLOR"); // load from localstorage
      if (
        loadedBackgroundColor === null &&
        appStyles?.backgroundColor !== "#282c34"
      ) {
        // if the loaded is null and we haven't set the background color
        // set the background color to default
        setAppStyles({ backgroundColor: "#282c34" });
        setAppClass("");
        // console.log("loading default");
        return;
      }

      if (
        loadedBackgroundColor !== null &&
        appStyles?.backgroundColor !== loadedBackgroundColor
      ) {
        // if it changed to something different
        // console.log("setting new");
        setAppStyles({ backgroundColor: localStorage.getItem("VRA_COLOR") });
        setAppClass("");
      }
    } else if (colorMode === "gradient") {
      // gradient
      setAppStyles({});
      setAppClass("gradient");
    } else {
      alert("Color Set Impossibly, Please Reset your Config");
    }
  };

  const tick = () => {
    setTime(new Date());
    colorFix();

    const fromTheStartOfDay = convertToEST(
      Math.round((Date.now() / 1000) % 86400)
    );

    const dayToday = new Date().getDay().toString();

    if (dayToday === "6" || dayToday === "7") {
      setIsWeekend(true);
      return;
    } else {
      if (isWeekend) setIsWeekend(false);
    }

    if (!!timesTable || !typeof timesTable === "undefined") {
      // data ready
      const dayMap = [...timesTable["any"], ...timesTable[dayToday]]; // ES6 deconstruct 2 elements into an array
      let update = false;

      dayMap.forEach((period, i) => {
        if (
          period.start <= fromTheStartOfDay &&
          period.end >= fromTheStartOfDay
        ) {
          setCurrentSlot(period);
          if (i !== dayMap.length - 1) {
            setNextSlot(dayMap[i + 1]);
          } else {
            setNextSlot(null);
          }
          update = true;
        }
      });

      if (!update) {
        setCurrentSlot("Bug Encountered: Please send time and date to Vlad");
      }
    }
  };

  useEffect(() => {
    colorFix();
    fetch("https://api.jsonbin.io/b/5fca6e3365c249127ba338d0")
      .then((response) => response.json())
      .then((data) => setTimesTable(data))
      .catch(() => {
        console.log("Probably Offline, PWA to the rescure");
        setTimesTable(defaultTimes);
      });
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  useEffect(() => {
    const timerID = setInterval(() => tick(), 1000);
    return () => {
      clearInterval(timerID); // runs every seccond, idk why
    };
  });

  return (
    <div style={appStyles} className={appClass}>
      <header className="App-header">
        <SettingsIcon
          times={timesTable}
          changeAppVisibility={setDisplayAppContents}
        />
        {displayAppContents && currentSlot?.primaryFocus !== 1 ? (
          <p className="corner-clock">
            {time.toLocaleTimeString()} {time.toLocaleDateString()}
          </p>
        ) : null}
        {displayAppContents && currentSlot?.primaryFocus === 1 ? (
          <p className="corner-clock">{time.toLocaleDateString()}</p>
        ) : null}
      </header>
      {displayAppContents ? (
        <header className="App-body">
          <Countdown timeSlot={currentSlot} isWeekend={isWeekend} time={time} />
          <Period
            timeSlot={currentSlot}
            nextSlot={nextSlot}
            isWeekend={isWeekend}
          />
        </header>
      ) : null}
    </div>
  );
}

export default AppContent;
