import React, { useEffect, useState } from "react";
import { calcRichContext } from "./helpers";

const Period = (props) => {
  const [display, setDisplay] = useState("loading...");
  const [nextDisplay, setNextDisplay] = useState("loading...");
  const [link, setLink] = useState(null);
  const [nextLink, setNextLink] = useState(null);

  // eslint-disable-next-line react-hooks/exhaustive-deps
  useEffect(() => {
    // This will run every frame update
    const { timeSlot, nextSlot, isWeekend } = props;

    if (isWeekend) {
      setDisplay("Imagine Having School on the Weekends");
      return;
    }

    // current period
    if (timeSlot !== undefined && timeSlot !== null) {
      const { prefix, link: gotLink, richContext } = calcRichContext(timeSlot);

      // set rich context for main
      setDisplay(`${prefix}${timeSlot.event}${richContext}`);
      setLink(gotLink);

      // next context
      if (nextSlot !== null && nextSlot !== undefined) {
        // console.log(nextSlot);
        const {
          prefix: nextPrefix,
          link: nextLink,
          richContext: nextRichContext,
        } = calcRichContext(nextSlot);
        setNextDisplay(`${nextPrefix}${nextSlot.event}${nextRichContext}`);
        setNextLink(nextLink);
      } else {
        setNextDisplay("No Next Event");
        setNextLink(null);
      }
    }
  });

  return (
    <div>
      <p
        onClick={() => {
          link !== null ? window.open(link) : console.log("nolink");
        }}
      >
        {display}
      </p>
      <p
        style={{ opacity: "50%" }}
        onClick={() => {
          nextLink !== null ? window.open(nextLink) : console.log("nonextlink");
        }}
      >
        {nextDisplay}
      </p>
    </div>
  );
};

export default Period;
