import React, { useState, useEffect } from "react";
import { dayToString } from "../helpers";
import Label from "./Label";

function Labels(props) {
  const { times } = props;
  const [editors, setEditors] = useState(null);

  useEffect(() => {
    if (!!times || !typeof times === "undefined") {
      // parsing
      const out = {};

      Object.keys(times).forEach((key) => {
        out[key] = [];
        times[key].forEach((key2) => {
          if (key2.richContext !== undefined) {
            // label
            const label = localStorage.getItem("VRA_" + key2.richContext);
            let labelValue = "";
            if (label !== null) labelValue = label;

            // link
            const link = localStorage.getItem("VRA_LINK_" + key2.richContext);
            let linkValue = "";
            if (link !== null) linkValue = link;

            out[key].push(
              <Label
                key={key2.richContext}
                key2={key2}
                linkValue={linkValue}
                labelValue={labelValue}
              />
            );
          }
        });
      });

      // displaying
      let outFormated = [];
      Object.keys(out).forEach((key) => {
        const dti = dayToString(key.toString());
        outFormated.push(<h2 key={dti}>{dti}</h2>);
        //outFormated.concat(out[key]);
        outFormated = [...outFormated, ...out[key]];
      });
      setEditors(outFormated);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return <>{editors}</>;
}

export default Labels;
