import React, { useEffect } from "react";
import times from "../svg/times.svg";
import ColorSelect from "./ColorSelect";
import Footer from "./Footer";
import Labels from "./Labels";

function SettingsModal(props) {
  useEffect(() => {
    // bind to escape key (same as pressing the x)
    const handleKeyPress = (e) => {
      if (e.key === "Escape") {
        e.preventDefault();
        props.handleCloseSettings();
      }
    };
    document.addEventListener("keydown", handleKeyPress);

    return () => {
      document.removeEventListener("keydown", handleKeyPress);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className="modal">
      <img
        src={times}
        alt="Close!"
        className="modal-close"
        onClick={() => props.handleCloseSettings()}
      />
      <div className="richContextEditors">
        <h2 className="header">Lables</h2>
        <Labels times={props.times} />
        <Footer />
      </div>
      <div>
        <ColorSelect />
      </div>
      <div>
        <br />
        <h2>Hello</h2>
        <p>Period Timers was coded by Vlad Bondar (25bondarv@mtps.us).</p>
        <p>
          Please send Bug Reports and Suggestions to me on Google Hangouts
          (Vladimir Bondar)
        </p>
      </div>
    </div>
  );
}

export default SettingsModal;
